.container_v3{
    display: flex;
    flex-direction: row;
    min-height: 25vh;
    text-align: center;
    align-items:center;
    justify-content: center;
    margin: 0;
    background:rgb(6, 6, 7);

}
.flex{
  display: flex;
  gap: 20px;
}
.footer-basic {
    padding:40px 0;
    color:#4b4c4d;
    flex-direction: row;
  }
  
  .footer-basic ul {
    padding:0;
    list-style:none;
    width: 100%;
    text-align:center;
    font-size:18px;
    line-height:1.6;
    margin-bottom:0;
  }
  
  .footer-basic li {
    padding:0 10px;
    cursor: pointer;
  }
  
  .footer-basic ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.8;
  }
  
  .footer-basic ul a:hover {
    opacity:1;
  }
  
  .footer-basic  {
    text-align:center;
    padding-bottom:25px;
  }
  
  .footer-basic  > a {
    font-size:24px;
    width:40px;
    height:40px;
    line-height:40px;
    text-align:center;
    border-radius:50%;
    border:1px solid #ccc;
    margin:0 8px;
    color:inherit;
    opacity:0.75;
  }
  
  .footer-basic .social > a:hover {
    opacity:0.9;
  }
  
  .footer-basic .copyright {
    margin-top:15px;
    text-align:center;
    font-size:13px;
    color:#aaa;
    margin-bottom:0;
  }